/**
 * http://usejsdoc.org/
 */
import request from 'request';
import logger from '../../logger';
import config from '../../../config';
import cst from '../../../utils/serverConstants';
import esErr from '../../../errors/errors';
import EventEmitter from 'events';
import os from 'os';
import process from 'process';

class JobEvent extends EventEmitter{
  constructor(){
    super();
  }
  emit(evtName){
    super.emit(evtName);
  }
  on(evtName, cb){
    super.on(evtName, cb);
  }
}


function getCronUrlBase(){
  let hostname = os.hostname();
  let hostnamePartialBaseUrlMaps = [
    {hostNamePartial: 'logantesting-candidate', url: 'https://logantesting-candidate.esna.com'},
    {hostNamePartial: 'logantesting-official', url: 'https://logantesting.esna.com'},
    {hostNamePartial: 'logan-production', url: 'https://logan.onesna.com'},
    {hostNamePartial: 'logan-candidate', url: 'https://logan-candidate.onesna.com'},
  ];
  
  for (let hostnamePartialBaseUrlItem of hostnamePartialBaseUrlMaps){
    if (hostname && hostname.indexOf(hostnamePartialBaseUrlItem.hostNamePartial) >= 0){
      return hostnamePartialBaseUrlItem.url;
    }
  }
  logger.warn('No hostname paritial is matched with this hostname ' + hostname);
  let svrPort = config.port || 5000;
  if (svrPort == 5000){
    return 'http://loganlocal.esna.com:5000';
  }
  else{
    logger.warn('Can use cron job function for there is no valid baseurl');
    return null;
  }
}

module.exports = function(agenda) {
  agenda.define('defer task', {priority: 'normal', concurrency: 5}, function(job, done) {
    console.log('running defer task');
    if (!job.esEvt){
      job.esEvt = new JobEvent();
    }
    let data = job.attrs.data || null;
    
    if (!data){
      logger.warn('Stop job for no data');
      job.fail('Stop job for no data');
      job.attrs.status = 'dispatched';
      job.save(done);
      return job.esEvt.emit('finished');
    }
    
    let url = data.url;
    let headers = {
        'Authorization': 'API_KEY ' + config.ESNA_API_KEY
    };
    if (url && typeof url == 'string' && url.length > 0){
      logger.info('Will send request to url ' + url);      
      //Before send out request close this job
      done();
      request({method:'POST',
               url: url,
               headers: headers,
               body: data,
               json: true,
               //forever: true,
               //timeout: 300000
               },
        (err, response, body) =>{
          if (err && (!err.code)){
            //Happen very critical error
            logger.info('Finish job for happen very critical error' + err.message);
            job.attrs.status = 'dispatched';
            job.remove();
          }
          else if (response && response.statusCode == cst.HttpSuccessStatus){
            logger.info('Finish job successfully');
            job.attrs.status = 'dispatched';
            job.remove();
          }
          else if (response && response.statusCode == cst.HttpErrorTaskQueueNeverTry){
            logger.warn('Stop job for No corresponding function with function key ' + data.keyString);
            job.fail(body);
            job.attrs.status = 'dispatched';
            job.remove();
          }
          else if (response && (response.statusCode == cst.HttpUnauthorizedStatus ||
            response.statusCode == cst.HttpForbiddenStatus))
          {
            logger.warn('Stop job for Authentication or authorization happen error');
            job.fail(body);
            job.attrs.status = 'dispatched';
            job.remove();
          }
          else if (response && response.statusCode == cst.HttpNotFoundStatus ){
            logger.warn('Stop job for Url not exsited error');
            job.fail(body);
            job.attrs.status = 'dispatched';
            job.remove();
          }
          else{
            let statusCode;
            if (response && response.statusCode){
              statusCode = response.statusCode;
            }
            console.log('Happen error check retry.', err, statusCode, body);
            if (data.attempts){
              data.attempt_times = data.attempt_times || 0;
              if (data.attempts > 0 && data.attempts > data.attempt_times){
                let backoff_seconds = data.backoff_seconds || 0;
                let nextScheduleTime = new Date();
                nextScheduleTime.setSeconds(nextScheduleTime.getSeconds() + backoff_seconds * (Math.pow(3, data.attempt_times)));
                data.attempt_times += 1;
                job.schedule(nextScheduleTime);
                job.attrs.status = 'dispatching';
                job.save();
                logger.info('Will try job at '+ nextScheduleTime.toString());
                return;
              }
              else if(data.attempts > 0 && data.attempts <= data.attempt_times){
                job.attrs.status = 'dispatched';
                job.remove();
                logger.info('The job stop retring for no more attempts');
              }
              else{
                //Endless attempt
                data.attempt_times += 1;
                let backoff_seconds = data.backoff_seconds || 0;
                backoff_seconds = (backoff_seconds) ? backoff_seconds || 2 : 2;
                let nextScheduleTime = new Date();
                nextScheduleTime.setSeconds(nextScheduleTime.getSeconds() + backoff_seconds * data.attempt_times);
                job.schedule(nextScheduleTime);
                job.attrs.status = 'dispatching';
                job.save();
                logger.info('Will try job at ' + nextScheduleTime.toString());
                return;
              }
            }
            else{
              logger.warn('Stop job for user require attempts 0');
              job.fail(err || body);
              job.attrs.status = 'dispatched';
              job.remove();
            }
          }
        }
      );
    }
    else{
      logger.warn('The url is invalid', url);
      job.fail(new esErr.ESErrors(esErr.TaskqueueInvalidUrl));
      job.attrs.status = 'dispatched';
      job.remove();
    }
    job.esEvt.emit('finished');
  });  
};



