import logger from '../modules/logger';

var prefix = 'task-server';

exports.info = function(args){
  logger.info(prefix, args);
};

exports.debug = function(args){
  logger.debug(prefix, args);
};

exports.warn = function(args){
  logger.warn(prefix, args);
};

exports.error = function(args){
  logger.error(prefix, args);
};

exports.sync = function(args){
  logger.sync(prefix, args);
};

exports.verbose = function(args){
  logger.verbose(prefix, args);
};

exports.silly = function(args){
  logger.silly('task-server', args);
};



